import React, { useState } from "react";
import {
  HeaderForm,
  ListUsers,
  UsersContainer,
} from "./elements";

import CardUser from '../CardUser'
import {Data1} from './Data'

function Users() {

  const [DataAux, setDataAux] = useState(Data1);


  return (
      <ListUsers>
        <HeaderForm>Lista de Usuarios</HeaderForm>
        <UsersContainer>
          {
            DataAux.map((data, i) => 
              <CardUser name={data.name} id={data.id} email={data.email} phone={data.phone} ci={data.ci}  key={i}/>
            )
          }
        </UsersContainer>
        
      </ListUsers>
  );
}
export default Users;
